
.nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    margin: 0 10px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    color: #060b35;
    font-size: 12px;
    font-weight: 500;
    line-height: 14px;
    text-transform: uppercase;
    text-decoration: none;
    padding: 23px 0;
    cursor: pointer;

    &:hover {
      color: $color;
    }
  }
}

.is-active {
  position: relative;
  color: $color;

  &:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 4px;
    left: 0;
    bottom: 0;
    background: $color;
    border-radius: 8px;
  }
}
